<template>
    <div class="articalDetails">
        <div class="art-base-info">
            <!-- <a href="#" style="font-size: 12px;">Online First, doi:10.32604/cmes.2020.011067 </a> -->
            <h3>
                Event-triggered stabilization of neural network via quantized sliding-model control
            </h3>
            <span style="display: block;color: #4f5671;line-height: 25px;margin-bottom: 10px;font-size: 12px;">
                Linxiang Qi1,  Shiping Wen2
            </span>
            <ul style="padding: 0;font-size: 12px;color: #4f5671;">
                <li>1 School of Computer Science and Engineering, University of Electronic Science and Technology of China, Chengdu, 611731, China</li>
                <li>2 Faculty of Engineering and Information Technology, University of Technology Sydney, NSW, 2007, Australia</li>
            </ul>
            <span style="font-size: 12px;color: #4f5671;">∗ Corresponding Author: Shiping Wen. </span>
        </div>

        <div class="download">
            <span><a href="https://stduestceducn-my.sharepoint.com/:b:/g/personal/201921080433_std_uestc_edu_cn/EcUJtVha00JGg0cz-0DP-dIBWiOMWwTV8ihf0Jw1uX84GQ?e=7KNimO" rel="nofollow" class="pdf">Download PDF</a>  </span>
        </div>

        <div class="abstract">
            <h3>Abstract</h3>
            <p>
                This paper focuses on the event-based stabilization of neural network via sliding-mode control (SMC) with quantized
state signal. By adopting a policy of event-triggered, and quantized state signal takes a value from a finite subset.
In addition, the quantized signal is sent to the sliding mode controller to generate the control signal and realize the
stabilization of the neural network. we firstly investigate two important parameters of quantization, such as quantified
sufficient parameter and the maximum value of the parameter that trigger the event. Then by designing a dynamical
sensitivity parameter which can adjust itself by the system state and event-triggered mechanism, we draw a conclusion
that the switching surface is reachable. At last we present some examples to prove the correctness of our conclusion
and make a summary.
            </p>
        </div>

        <div class="keywords">
            <h3>Keywords</h3>
            <p>neural network, sliding-mode control, quantization, robust stabilization</p>
        </div>


    </div>
</template>

<script>
